
  .article-content {
    margin-left: 1%;
    margin-right: 1%;
  }

    #h1 {
      display: flex;
      justify-content: center;
      padding-top: 5%;
      padding-bottom: 3%;
    }
    
   
    .goBack-btn-container {
      position: relative;
      padding: 1%;
      padding-bottom: 3%;
    }

    .checkout-btn-container {
      position: relative;
      padding: 1%;
      padding-bottom: 17%;
    }
      #btn-go-to-checkout{
        position: absolute;
        right: 0;
        bottom: 0;
      }
      
      
    

    #btn-go-back-to-products, #btn-go-to-checkout {
      background-color: rgb(235, 245, 233);
      
    }      

    #clear-btn-container {
      padding: 1%;  
      
    }

    
    .labels {
      margin: 1%;
      font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue",
        Helvetica, Arial, sans-serif;
      font-weight: 100;
  
      border-top: 1px solid grey;
      padding-top: 1%;
  
      display: grid;
      gap: 1em;
      grid-auto-flow: column;
      grid-auto-columns: 1fr;
  
      padding-bottom: 1%;
      border-bottom: 1px solid grey;
    }
  
    
    .product-item {
      margin: 1%;
      display: grid;
      align-items: center;
      justify-content: center;
      gap: 1em;
      grid-auto-flow: column;
      grid-auto-columns: 1fr;
      padding: 1%;
      border-bottom: 1px solid rgb(206, 206, 206);
    }
     
      .product-title {
        font-size: 1em;
      }
  
     
      .product-image {
        width: 30%;
      }
  
      
      .quantity-container {
        display: flex;
        flex-direction: row;
        gap: 10%;
        align-items: center;
  
      }
    
  
    
    #min-varukorg {
      position: relative;
    }
    #totalSum {
      position: absolute;
      right: 5%;
    }
  
  
  
  .footer-container {
    display: flex;
    flex-direction: column;
    margin-top: 3%;
    width: 100%;
    background-color: rgb(96, 144, 96);
    bottom: 0%;
  }

    .footer-text {
      margin-top: 1.5%;
      width: 100%;
      justify-content: space-evenly;
      align-items: center;
      font-size: 0.8rem;
      text-align: center;
      line-height: normal;
    }
  


